<template>
  <div class="account-suspended-container">
    <div class="suspended-card">
      <div class="icon-container">
        <i class="pi pi-lock" style="font-size: 4rem; color: #f44336;"></i>
      </div>
      <h1 class="title">Compte Suspendu</h1>
      <p class="message">
        Votre compte a été temporairement suspendu par l'administrateur.
      </p>
      <p class="instructions">
        Pour régler ce problème et réactiver votre compte, veuillez contacter l'administrateur système.
      </p>
      <div class="contact-info">
        <i class="pi pi-envelope"></i>
        <span>Contactez l'administrateur pour plus d'informations</span>
      </div>
      <Button 
        label="Se déconnecter" 
        icon="pi pi-sign-out" 
        class="p-button-danger logout-btn"
        @click="logout"
      />
    </div>
  </div>
</template>

<script>
import { defineComponent } from 'vue';
import Button from 'primevue/button';

export default defineComponent({
  name: 'AccountSuspendedView',
  components: {
    Button
  },
  methods: {
    async logout() {
      try {
        await this.$store.dispatch('logout');
        this.$router.push('/connexion');
      } catch (error) {
        console.error('Erreur lors de la déconnexion:', error);
        // Forcer la redirection même en cas d'erreur
        this.$router.push('/connexion');
      }
    }
  }
});
</script>

